<template>
    <el-container>
      <el-header class="header-path" style="height:40px;">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-header>
    <div style="margin-bottom:10px; text-align:left;">
        <!-- -->
        <el-select style="width:150px" v-model="search.status" clearable placeholder="">
            <el-option v-for="item in ststatus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <el-input
            style="width:180px"
            placeholder=""
            v-model="search.key"
            clearable>
        </el-input>
        <!-- -->
        <el-button type="primary" plain @click="getData()"></el-button>
        <div style="display:inline-block;"><el-alert title="" type="success"></el-alert></div>
        
    </div>
    <el-table
        stripe
        border
        highlight-current-row
        @row-click="handleCurrentChange"
        height="550px"
        :data="tableData"
        style="width: 100%"
        :default-sort = "{prop: 'id', order: 'descending'}">
        <el-table-column
            prop="category"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="name"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="id"
            label=""
            sortable
            >
        </el-table-column>
        <el-table-column
            prop="type"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="size"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="price"
            label=""
            sortable>
        </el-table-column>

        <el-table-column
            :formatter="buydateFormatter"
            prop="date"
            width="120px"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="manufacturer"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            :formatter="expirationdateFormatter"
            prop="expirationdate"
            width="120px"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            :formatter="stateFormatter"
            prop="operator"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            :formatter="stateFormatter"
            prop="status"
            label=""
            sortable>
        </el-table-column>
        <el-table-column label="" width="160px">

            <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                    <el-row>
                        <el-col :span="8"><el-button
                            size="mini"
                            @click="handleRepair( scope.row)"></el-button></el-col>
                        <el-col :span="8"><el-button
                            size="mini"
                            type="danger"
                            @click="handleScrap( scope.row)"></el-button></el-col>
                        <el-col :span="8"><el-button
                            size="mini"
                            type="primary"
                            @click="toDetail( scope.row)"></el-button></el-col>
                    </el-row>
                    <div slot="reference" class="name-wrapper">
                        <el-tag size="medium"></el-tag>
                    </div>
                </el-popover>
            </template>
        </el-table-column>
    </el-table>
    

    <el-dialog
        title=""
        :visible.sync="dialogVisibleRepair"
        width="30%"
        :before-close="handleClose">
        <span></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleRepair = false"> </el-button>
            <el-button type="primary" @click="okRepair()"> </el-button>
        </span>
    </el-dialog>

    <el-dialog
        title=""
        :visible.sync="dialogVisibleScrap"
        width="30%"
        :before-close="handleClose">
        <span><br></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleScrap = false"> </el-button>
            <el-button type="primary" @click="okScrap()"> </el-button>
        </span>
    </el-dialog>



    </el-container>
</template>

<script>
export default {
    mounted(){
        this.getData(); //
    },
    data() {
        return {
            ststatus: [{label:'', value:'0'},{label:'', value:''},{label:'', value:''},{label:'', value:''}],
            dialogVisibleRepair: false,
            dialogVisibleScrap: false,
            search:{
                status:'',
                key:''
            },
            tableData:[],
            idNow:'',
            nameNow:''
        }
    },
    methods: {
      formatter(row, column) {
        return row.address;
      },
      handleCurrentChange(val) {
        this.currentRow = val;
      },
      
      add0(m){return m<10?'0'+m:m },
      expirationdateFormatter(row,column){
            var expirationdate = new Date(parseInt(row.expirationdate));
            var year=expirationdate.getFullYear();
            var month=expirationdate.getMonth()+1;
            var day=expirationdate.getDate();
            return year+"-"+this.add0(month)+"-"+this.add0(day);
      },
      buydateFormatter(row,column){
            var expirationdate = new Date(parseInt(row.date));
            var year=expirationdate.getFullYear();
            var month=expirationdate.getMonth()+1;
            var day=expirationdate.getDate();
            return year+"-"+this.add0(month)+"-"+this.add0(day);
      },
      handleRepair(row) {
        this.idNow = row.id;
        this.nameNow = row.name;
        this.dialogVisibleRepair = true;
      },
      handleScrap(row) {
        this.idNow = row.id;
        this.nameNow = row.name;
        this.dialogVisibleScrap = true;
      },
      okRepair(){
            var _this=this;
            //
            this.axios.get('equipment/repair?id='+ _this.idNow+'&responsible='+ '--'+'&name='+_this.nameNow )
                .then(function (response) {
                    alert(response.data);
                    if(response.data=='success'){
                        _this.$message({
                            message: '',
                            type: 'success'
                            });
                        _this.$router.replace({path: '/repairDev/list'});
                    }
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
            this.dialogVisibleRepair = false;
      },
      okScrap(){
            var _this=this;
            //
            this.axios.get('equipment/scrap?id='+ _this.idNow +'&responsible='+ '--'+'&name='+_this.nameNow )
                .then(function (response) {
                    alert(response.data);
                    if(response.data=='success'){
                        _this.$message({
                            message: '',
                            type: 'success'
                            });
                        _this.$router.replace({path: '/scrapDev/list'});
                    }
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
            this.dialogVisibleScrap = false;
      },
      toDetail(row) {
        this.$router.push({name: 'manageDetail', params:{ val:row }});
      },

      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      getData(){
            if(!this.search.status){
                var status = '0';
            }else{var status = this.search.status}
            if(!this.search.key){
                var key = '0';
            }else{var key = this.search.key}
            var _this=this;
            //
            this.axios.get('equipment/find?status='+status+'&info='+key)
                .then(function (response) {
                    //response
                    //
                    var dataList=response.data;
                    _this.tableData=dataList;
                    _this.$message({
                        message: '',
                        type: 'success'
                        });
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
      }
    }
}
</script>

<style scoped></style>


