<template>
    <el-container>
      <el-header class="header-path">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-header>
      <main style="text-align:left;">
        <el-form :label-position="labelPosition" label-width="110px" :inline="true" :model="kcChange">
            <el-form-item label="">
                <el-input v-model="addBuy.category"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="addBuy.name"></el-input>
            </el-form-item>

        </el-form>
        <el-form :inline="true">
            <el-form-item label="" label-width="110px">
                <el-input v-model="addBuy.type"></el-input>
            </el-form-item>

            <el-form-item label="" label-width="110px">
                <el-input v-model="addBuy.size"></el-input>
            </el-form-item>

        </el-form>
        <el-form :inline="true" label-width="110px">
            <el-form-item label="">
                <el-input v-model="addBuy.price"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="addBuy.count"></el-input>
            </el-form-item>

        </el-form>
        <el-form :inline="true" label-width="110px">
            <el-form-item label="">
                <el-input v-model="addBuy.manufacturer"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="addBuy.expirationdate"></el-input>
            </el-form-item>

        </el-form>
        <el-form :inline="true" label-width="110px">

            <el-form-item label="">
                <el-input v-model="addBuy.operator"></el-input>
            </el-form-item>

        </el-form>
        
        <el-form :inline="true" size="mini" style="text-align:center;">
            <el-button type="primary" @click="button_click('submit')"></el-button>
            <el-button type="primary" @click="button_click('return')"></el-button>
        </el-form>
      </main>



    </el-container>
</template>

<script>

export default {
//-----------json
    name: "add",

    data() {
        return {
            kcChange:{},
            addBuy: {
                status: ''
            }
        };
    },
    methods: {
        button_click(kind){
            if(kind=='submit'){
                this.addData();
                return;
            }else if(kind=='return'){
                this.$router.push({path:'/addDev/list'});
                return;
            }
        },
        addData(){
            var _this=this;
            //

            this.axios.post('buy/add', _this.addBuy)
                .then(function (response) {
                    //response
                    //
                    alert(response.data);
                    _this.$router.push({path:'/addDev/list'});
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
        }

    }
};
</script>

<style scoped>
.el_select {
    width:150px;
}
.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item {
    margin-bottom: 8px;
}
.el-form-item {
    margin-bottom: 8px;
}
</style>


