<template>
    <el-container>
      <el-header class="header-path" style="height:40px;">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-header>
    <div style="margin-bottom:10px; text-align:left;">
        <!-- -->
        <el-select style="width:150px" v-model="search.status" clearable placeholder="">
            <el-option v-for="item in ststatus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <el-date-picker
            v-model="search.data"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions2">
        </el-date-picker>
        <el-input
            style="width:180px"
            placeholder=""
            v-model="search.key"
            clearable>
        </el-input>
        <!-- -->
        <el-button type="primary" plain @click="getData()"></el-button>
        <div style="display:inline-block;"><el-alert title="" type="success"></el-alert></div>
        
    </div>
    <el-table
        stripe
        border
        highlight-current-row
        @row-click="handleCurrentChange"
        height="550px"
        :data="tableData"
        style="width: 100%"
        :default-sort = "{prop: 'xqdm', order: 'descending'}">
        <el-table-column
            :formatter="dateFormatter"
            prop="date"
            width="120px"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="name"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="price"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="equid"
            label=""
            sortable
            >
        </el-table-column>
        <el-table-column
            prop="responsible"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="status"
            label=""
            sortable>
        </el-table-column>
       
        <el-table-column label="" width="160px">
            <template slot-scope="scope">
                <el-button
                    size="mini"
                    @click="handleScrap(scope.row)">
                </el-button>
            </template>
        </el-table-column>
    </el-table>

    </el-container>
</template>

<script>
export default {
    mounted(){
        this.getData(); //
    },
    data() {
        return {
            ststatus: [{label:'', value:'0'},{label:'', value:''},{label:'', value:''},{label:'', value:''}],
            search:{
                data:'',
                status:'',
                key:'',
                timeStart:'',
                timeEnd:''
            },
            tableData:[],



            
        pickerOptions2: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        }

        }
    },
    methods: {

            add0(m){return m<10?'0'+m:m },
      dateFormatter(row,column){
            var expirationdate = new Date(parseInt(row.date));
            var year=expirationdate.getFullYear();
            var month=expirationdate.getMonth()+1;
            var day=expirationdate.getDate();
            return year+"-"+this.add0(month)+"-"+this.add0(day);
      },
      handleCurrentChange(val) {
        this.currentRow = val;
      },
      handleScrap(row) {
        this.$router.replace({name: 'isScrap', params:{ val:row }});
      },

      getData(){
            if(!this.search.status){
                var status = '0';
            }else{var status = this.search.status;}
            if(!this.search.key){
                var key = '0';
            }else{var key = this.search.key;}
            if(!this.search.data){
                var timeStart = '0';
                var timeEnd = '0';
            }else{
                var timeStart = this.search.data.toString().split(',')[0];
                var timeEnd = this.search.data.toString().split(',')[1];
            }
            var _this=this;
            //
            this.axios.get('scrap/find?status='+status+'&info='+key+'&timeStart='+timeStart+'&timeEnd='+timeEnd)
                .then(function (response) {
                    //response
                    //
                    var dataList=response.data;
                    _this.tableData=dataList;
                })
                .catch(function (error) {
                    console.log(error);
                alert(",");
                });
      }
    }
}
</script>

<style scoped></style>


