<template>
    <el-container>
      <el-header class="header-path" style="height:40px;">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-header>
    <div style="margin-bottom:10px; text-align:left;">
        <el-date-picker
            v-model="value7"
            type="daterange"
            align="right"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            :picker-options="pickerOptions2">
        </el-date-picker>
        <el-input
            style="width:180px"
            placeholder=""
            v-model="search.key"
            clearable>
        </el-input>
        <!-- -->
        <el-button type="primary" plain></el-button>
        <!-- <div style="display:inline-block;">
            <el-alert title="" type="success"></el-alert>
        </div> -->
        
    </div>
    <el-table
        stripe
        border
        highlight-current-row
        @row-click="handleCurrentChange"
        height="350px"
        :data="tableData"
        style="width: 100%"
        :default-sort = "{prop: 'xqdm', order: 'descending'}">
        <el-table-column
            prop="xqdm"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="xqmc"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="xqdm"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="xqjp"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="zrr"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="xqmc"
            label=""
            sortable>
        </el-table-column>
    </el-table>

    <el-dialog
        title=""
        :visible.sync="dialogVisibleRepair"
        width="30%"
        :before-close="handleClose">
        <span></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleRepair = false"> </el-button>
            <el-button type="primary" @click="okRepair()"> </el-button>
        </span>
    </el-dialog>

    <el-dialog
        title=""
        :visible.sync="dialogVisibleScrap"
        width="30%"
        :before-close="handleClose">
        <span><br></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleScrap = false"> </el-button>
            <el-button type="primary" @click="okScrap()"> </el-button>
        </span>
    </el-dialog>



    </el-container>
</template>

<script>
export default {
    mounted(){
        this.getData(); //
    },
    data() {
        return {
            ststatus: [{label:'', value:''},{label:'', value:''}],
            dialogVisibleRepair: false,
            dialogVisibleScrap: false,
            search:{
                status:'',
                key:''
            },
            tableData:[],


        pickerOptions2: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        value7: ''


        
        }
    },
    methods: {
      formatter(row, column) {
        return row.address;
      },
      handleCurrentChange(val) {
        this.currentRow = val;
      },
      handleRepair(index, row) {
        this.dialogVisibleRepair = true;
      },
      okRepair(){
            this.dialogVisibleRepair = false;
            this.$message({
                message: '',
                type: 'success'
                });
      },
      handleScrap(index, row) {
        this.dialogVisibleScrap = true;
      },
      okScrap(){
          this.dialogVisibleScrap = false;
          this.$message({
                message: '',
                type: 'success'
                });
      },
      toDetail(index, row) {
            this.$router.push({path: '/manageDev/detail',
                params:{ val:row ,id: row.xqdm+new Date().getSeconds(), type: 'change'}});
      },
      //
      stateFormatter(row,column){
        let state = row.state;
        if(state === '0'){return ''} else {return ''}
      },

      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      getData(){
            var _this=this;
            //
            this.axios.get('SysXq/getAll')
                .then(function (response) {
                    //response
                    //
                    var dataList=response.data;
                    _this.tableData=dataList;
                })
                .catch(function (error) {
                    console.log(error);
                    _this.tableData=[{xqdm:"12"}];
                //alert(",");
                });
      }
    }
}
</script>

<style scoped></style>


