<template>
    <el-container>
      <el-header class="header-path">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item>{</el-breadcrumb-item>
        </el-breadcrumb>
      </el-header>
      <main style="text-align:left;">
        <el-form :label-position="labelPosition" label-width="110px" :inline="true" :model="kcChange">
            <el-form-item label="">
                <el-input v-model="tabledetail.category"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="tabledetail.name"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="tabledetail.id" :disabled="true"></el-input>
            </el-form-item>
        </el-form>
        <el-form :inline="true">
            <el-form-item label="" label-width="110px">
                <el-input v-model="tabledetail.type"></el-input>
            </el-form-item>

            <el-form-item label="" label-width="110px">
                <el-input v-model="tabledetail.size"></el-input>
            </el-form-item>

        </el-form>
        <el-form :inline="true" label-width="110px">
            <el-form-item label="">
                <el-input v-model="tabledetail.price"></el-input>
            </el-form-item>


        </el-form>
        <el-form :inline="true" label-width="110px">
            <el-form-item label="">
                <el-input v-model="tabledetail.date"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="tabledetail.manufacturer"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="tabledetail.expirationdate"></el-input>
            </el-form-item>

        </el-form>
        <el-form :inline="true" label-width="110px">

            <el-form-item label="">
                <el-input v-model="tabledetail.operator"></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="tabledetail.status"></el-input>
            </el-form-item>
        </el-form>
        
        <el-form :inline="true" size="mini" style="text-align:center;">
            <el-button type="primary" @click="button_click('repair')"></el-button>
            <el-button type="primary" @click="button_click('scrap')"></el-button>
            <el-button type="primary" @click="button_click('return')"></el-button>
        </el-form>
      </main>
    <div></div>
      
    <el-dialog
        title=""
        :visible.sync="dialogVisibleRepair"
        width="30%"
        :before-close="handleClose">
        <span></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleRepair = false"> </el-button>
            <el-button type="primary" @click="okRepair()"> </el-button>
        </span>
    </el-dialog>

    <el-dialog
        title=""
        :visible.sync="dialogVisibleScrap"
        width="30%"
        :before-close="handleClose">
        <span><br></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisibleScrap = false"> </el-button>
            <el-button type="primary" @click="okScrap()"> </el-button>
        </span>
    </el-dialog>

    </el-container>
</template>

<script>

export default {
//-----------json
    name: "detail",
    created() {
        this.tabledetail.date = this.dateFormatter(this.tabledetail.date);
        this.tabledetail.expirationdate = this.dateFormatter(this.tabledetail.expirationdate);
    },
    data() {
        return {
            tabledetail: this.$route.params.val,

            dialogVisibleRepair: false,
            dialogVisibleScrap: false
        };
    },
    methods: {
        button_click(kind){
            if(kind=='return'){
                this.$router.push({path: '/manageDev/list'});
                return;
            }else if(kind=='repair'){
                this.dialogVisibleRepair = true;
            }else if(kind=="scrap"){
                this.dialogVisibleScrap = true;
            }
        },
        okRepair(){
            var _this=this;
            //
            this.axios.get('equipment/repair?id='+ _this.tabledetail.id +'&responsible='+ '--'+'&name='+_this.tabledetail.name )
                .then(function (response) {
                    alert(response.data);
                    if(response.data=='success'){
                        _this.$message({
                            message: '',
                            type: 'success'
                            });
                    }
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
            this.dialogVisibleRepair = false;
        },      
      add0(m){return m<10?'0'+m:m },
      dateFormatter(date){
            var expirationdate = new Date(parseInt(date));
            var year=expirationdate.getFullYear();
            var month=expirationdate.getMonth()+1;
            var day=expirationdate.getDate();
            return year+"-"+this.add0(month)+"-"+this.add0(day);
      },
        handleClose(done) {
            this.$confirm('')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        },
        okScrap(){
            var _this=this;
            //
            this.axios.get('equipment/scrap?id='+ _this.tabledetail.id +'&responsible='+ '--'+'&name='+_this.tabledetail.name)
                .then(function (response) {
                    alert(response.data);
                    if(response.data=='success'){
                        _this.$message({
                            message: '',
                            type: 'success'
                            });
                    }
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
            this.dialogVisibleScrap = false;
        },


    }
};
</script>

<style scoped>
.el_select {
    width:150px;
}
.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item {
    margin-bottom: 8px;
}
.el-form-item {
    margin-bottom: 8px;
}
</style>


