<template>
    <el-container>
      <el-header class="header-path" style="height:40px;">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </el-header>
    <div style="margin-bottom:10px; text-align:left;">
        <!-- -->
        <el-select style="width:150px" v-model="search.status" clearable placeholder="">
            <el-option v-for="item in ststatus"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <el-input
            style="width:180px"
            placeholder=""
            v-model="search.key"
            clearable>
        </el-input>
        <!-- -->
        <el-button type="primary" plain @click="getData()"></el-button>
        <div style="display:inline-block;"><el-alert title="" type="success"></el-alert></div>
        
    </div>
    <el-table
        stripe
        border
        highlight-current-row
        @row-click="handleCurrentChange"
        height="550px"
        :data="tableData"
        style="width: 100%"
        :default-sort = "{prop: 'xqdm', order: 'descending'}">
        <el-table-column
            prop="id"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="category"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="name"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="type"
            label=""
            sortable
            >
        </el-table-column>
        <el-table-column
            prop="size"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="price"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="count"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="manufacturer"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            :formatter="expirationdateFormatter"
            width="120px"
            prop="expirationdate"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="operator"
            label=""
            sortable>
        </el-table-column>
        <el-table-column
            prop="status"
            label=""
            sortable>
        </el-table-column>       
        <el-table-column label="" width="160px">

            <template slot-scope="scope">
                <el-popover trigger="hover" placement="top">
                    <el-row>
                        <el-col :span="8">
                            <el-button size="mini" type="primary" @click="handleState('', scope.row)"></el-button>
                        </el-col>
                        <el-col :span="8">
                            <el-button size="mini" type="primary"  @click="handleState('', scope.row)"></el-button>
                        </el-col>
                        <el-col :span="8">
                            <el-button size="mini" @click="handleState('', scope.row)"></el-button>
                        </el-col>
                    </el-row>
                    <div slot="reference" class="name-wrapper">
                        <el-tag size="medium"></el-tag>
                    </div>
                </el-popover>
            </template>
        </el-table-column>
    </el-table>
    

    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        width="30%"
        :before-close="handleClose">
        <span></span>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false ;"> </el-button>
            <el-button type="primary" @click="okRepair()"> </el-button>
        </span>
    </el-dialog>

    </el-container>
</template>

<script>
export default {
    mounted(){
        this.getData(); //
    },
    data() {
        return {
            ststatus: [
                {label:'', value:'0'},
                {label:'', value:''},
                {label:'', value:''},
                {label:'', value:''},
                {label:'', value:''}
            ],
            dialogVisible: false,
            search:{
                status:'',
                key:''
            },
            changes:{
                state:'',
                id:''
            },
            tableData: [],
        }
    },
    methods: {
      formatter(row, column) {
        return row.address;
      },
      add0(m){return m<10?'0'+m:m },
      expirationdateFormatter(row,column){
            var expirationdate = new Date(parseInt(row.expirationdate));
            var year=expirationdate.getFullYear();
            var month=expirationdate.getMonth()+1;
            var day=expirationdate.getDate();
            return year+"-"+this.add0(month)+"-"+this.add0(day);
      },
      handleCurrentChange(val) {
        this.currentRow = val;
      },
      handleState(state, row) {
        this.dialogVisible = true;
        this.changes.state = state;
        this.changes.id = row.id;
      },
      okRepair(){
            this.dialogVisible = false;
            this.modifyStatus(this.changes.state, this.changes.id);
            this.$message({
                message: '',
                type: 'success'
            });
        //    this.$router.replace({name: 'xqdmRightForm',
        //    params:{ val:row ,change_id: row.xqdm+new Date().getSeconds(), type: 'change'}});
      },
        handleClose(done) {
            this.$confirm('')
            .then(_ => {
                done();
            })
            .catch(_ => {});
        },
      modifyStatus(status, id){
            var _this=this;
            //
            this.axios.get('buy/modify?id= '+id+'&status='+status)
                .then(function (response) {
                    alert(response.data);
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
      },
      getData(){
            if(!this.search.status){
                var status = '0';
            }else{ var status =  this.search.status;}
            if(!this.search.key){
                var key = '0';
            }else{ var key = this.search.key;}
            
            var _this=this;
            //
            this.axios.get('buy/find?status='+status+'&info='+key)
                .then(function (response) {
                    //response
                    //
                    var dataList=response.data;
                    _this.tableData=dataList;

                    _this.$message({
                            message: '',
                            type: 'success'
                        });
                })
                .catch(function (error) {
                    console.log(error);
                    alert(",");
                });
      }
    }
}
</script>

<style scoped></style>


